import uniPopup from '@/components/uni-popup/uni-popup.vue';
export default {
	components: {
		uniPopup
	},
	data() {
		return {
			src: 'https://img-cdn-qiniu.dcloud.net.cn/hello-nvue-swiper-vertical-01.mp4',
			list: [
				{ id: 1, adress: '美星镇-新街村' },
				{ id: 2, adress: '美星镇-新街村' },
				{ id: 3, adress: '美星镇-新街村' },
				{ id: 4, adress: '美星镇-新街村' },
				{ id: 5, adress: '美星镇-新街村' },
				{ id: 6, adress: '美星镇-新街村' },
				{ id: 7, adress: '美星镇-新街村' },
				{ id: 8, adress: '美星镇-新街村' },
				{ id: 111, adress: '美星镇-新街村' },
				{ id: 12, adress: '美星镇-新街村' },
				{ id: 13, adress: '美星镇-新街村' },
				{ id: 41, adress: '美星镇-新街村' },
				
			],
			currentRate: 1, //当前播放id
			rateShow: false, //右侧弹出选项
			detailData: {},
			title: '2019-12-12 13:24:30 星期三',
			adress: '美星镇-新街村',
			isMenu: false,
			isshow: false,
			isMenu1: true,
			isIos: true,
			feedbackContent: ''
		};
	},
	onLoad(options) {
		this.detailData = JSON.parse(options.data);
		this.loadNewsList();
	},
	onReady: function(res) {

		this.videoContext = uni.createVideoContext('myVideo');

		this.videoContext.requestFullScreen();
		this.videoContext.play();
	},
	methods: {
		fullscreenchange(event) {
			console.log(event.detail.fullScreen);

			// 	if (!event.detail.fullScreen) {
			// 		this.backup()
			// 	}
		},
		backup() {
			uni.navigateBack({
				delta: 1
			});
		},
		clk() {
			let that = this;
			that.isMenu = !that.isMenu;
		},
		loadNewsList() {
			console.log(this.detailData);
		},
		videoErrorCallback: function(e) {
			uni.showModal({
				content: e.target.errMsg,
				showCancel: false
			});
		},
		//一键求助弹出框

		sosPopup() {
			console.log('我要反馈');
			this.videoContext.exitFullScreen();
			this.$nextTick(() => {
				this.$refs.showtip1.open();
				this.isshow = true;
				this.isMenu1 = true;
			});
		},
		cancel(type) {
			this.$refs.showtip1.close();
			this.videoContext.requestFullScreen({direction:90});
			this.isshow = false;
		},
		//侧边导航栏
		choosed() {
			this.isMenu1 = false;
			if (uni.getSystemInfoSync().platform == 'android') {
				this.isIos = false;
				this.videoContext.exitFullScreen();
			}
		},
		closeedMenu() {
			this.isMenu1 = true;
		},
		//选择视频播放
		switchRate(id) {
			let that = this;
			that.currentRate = id;

			this.isMenu1 = true;
			if (uni.getSystemInfoSync().platform == 'android') {
				this.videoContext.requestFullScreen({direction:90});
			}
			console.log(id);
		}
	}
};
